import React, {Component} from 'react';
import {
    Button,
    Col,
    Form,
    FormGroup,
    Input,
    Label,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader,
    Nav,
    NavItem
} from 'reactstrap';
import Table from '../../../components/Table/Table'
import MiniButton from '../../../components/MiniButton'
import SweetAlertExt from '../../../components/SweetAlertExt'
import Notify from 'react-notification-alert';
import validator from 'validator'
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import Alert from "react-s-alert";
import {validFormType} from "../common/validation/fieldValidation";
import StickButton from "../../../components/StickButton";
import Joyride from "react-joyride";

class FormTypePage extends Component {

    constructor(props) {
        super(props);
        this.notify = React.createRef();
        this.state = {
            alert: null,
            session: null,
            loading: true,
            displayCreateUpdateDialog: false,
            valid: null,
            manual: false,
            content: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            searchKey: "",
            steps: [
                {
                    target: '.main-panel',
                    content: "form turu tanimi",
                    placement: "center",
                },
                {
                    target: '.zocial',
                    content: 'Bordro yüklemek için lütfen "Ücret Bordrosu Yükle" butonuna tıklayınız.',
                    placement: "bottom-start",
                },
            ],
            locale: {
                back: 'Geri',
                close: 'Kapat',
                last: 'Son',
                next: 'İleri',
                skip: 'Atla',
            },
            joyrideActive: false,
            shouldRun: true,
            stop: false
        };

        this.editValidateDef = {
            category: v => !validator.isEmpty(v),
        };

        this.fetchData = this.fetchData.bind(this);
        this.searchWithKey = this.searchWithKey.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.createClicked = this.createClicked.bind(this);
        this.editClicked = this.editClicked.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.deleteClicked = this.deleteClicked.bind(this);
        this.editHandleInputChange = this.editHandleInputChange.bind(this);
        this.findFormTemplatesByFormType = this.findFormTemplatesByFormType.bind(this);
        this.callback = this.callback.bind(this);
    }

    async findFormTemplatesByFormType(row) {
        let id = row.id;
        let params = {
            url: `/formtemplate/countFormTemplate`,
            method: "post",
            data: id
        };

        let result = null;

        await requestK(params)
            .then(function (response) {
                result = response.data;
            })
            .catch(function (error) {
                showAxiosError(error);
            });
        return parseInt(result);
    }

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    searchWithKey(e) {
        this.setState({searchKey: e.target.value, manual: true});
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/formtype/readFormTypeAllPageable?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "post",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    content: response.data.content,
                    loading: false,
                    searching: false
                });
                self.props.reload(false);
            }).catch(function (error) {
            self.setState({loading: false, searching: false});
            self.props.reload(false);
            showAxiosError(error);
        });
    }

    createClicked() {
        this.newData = true;
        this.setState({
            selected: {
                category: ""
            },
            valid: {},
            displayCreateUpdateDialog: true
        });
    }

    editClicked(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone,
            valid: {},
            displayCreateUpdateDialog: true
        });
    }

    saveClicked() {
        let formtype = Object.assign({}, this.state.selected);
        formtype.category = formtype.category.trim();
        if (formtype.category && (formtype.category.toString().toUpperCase() === "ŞABLONSUZ" || formtype.category.toString().toUpperCase() === "ŞABLONSUZ TASLAK")) {
            this.setState({
                alert: (
                    <SweetAlertExt showCancel warning
                                   confirmBtnText="Tamam"
                                   cancelBtnText="Hayır"
                                   confirmBtnBsStyle="danger"
                                   cancelBtnBsStyle="default"
                                   title="Form Türü şeçilemez?"
                                   onConfirm={() => this.setState({alert: null})}
                                   onCancel={() => this.setState({alert: null})}
                    >
                        Form Türü olarak 'Şablonsuz veya Şablonsuz Taslak' seçilemez. Lütfen geçerli bir seçim yapınız.
                    </SweetAlertExt>
                )
            });
        } else {
            let result = validFormType(formtype);
            let valid = result.valid;
            if (valid) {
                let self = this;
                let params = {
                    url: `/formtype/saveFormType`,
                    method: "post",
                    data: formtype
                };
                self.setState({disabledSaveBtn: true});
                requestK(params)
                    .then(function (response) {
                        let message = response.data;
                        self.setState({
                            selected: null,
                            displayCreateUpdateDialog: false,
                            manual: true,
                            disabledSaveBtn: false
                        });
                        Alert.success(message, {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                    }).catch(function (error) {
                    showAxiosError(error);
                    self.setState({disabledSaveBtn: false});
                });
            } else {
                Alert.error(result.errorMessage, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 8000
                });
            }
        }

    }

    deleteClicked(row) {
        let self = this;
        let params = {
            url: `/formtype/deleteFormType`,
            method: "post",
            data: row
        };
        self.setState({disabledDeleteBtn: true});
        requestK(params)
            .then(function (response) {
                let message = response.data;
                self.setState({
                    selected: null,
                    displayCreateUpdateDialog: false,
                    manual: true,
                    alert: null,
                    disabledDeleteBtn: false
                });
                Alert.success(message, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({loading: false, alert: null, disabledDeleteBtn: false});
        });
    }

    editHandleInputChange(event) {
        const target = event.target;
        let value = target.type === 'checkbox' ? target.checked : this.upperCase(target);
        let name = target.name;

        let valid = this.editValidateDef[name] && this.editValidateDef[name](value);
        this.setState((prevState) => {
            prevState.selected[name] = value;
            prevState.valid[name] = valid;
            return {
                selected: prevState.selected,
                valid: prevState.valid
            };
        });
    }

    upperCase(target) {
        return target.value.toString().replace("i", "İ").toUpperCase();
    }

    editClassName(name) {
        let valid = this.state.valid[name];
        if (typeof valid !== "boolean")
            return undefined;
        else {
            return {
                valid: valid,
                invalid: !valid
            };
        }
    }

    async countChecker(row) {
        if (await this.findFormTemplatesByFormType(row) === 0) {
            {
                this.setState({
                    alert: (
                        <SweetAlertExt showCancel warning
                                       confirmBtnText="Evet"
                                       cancelBtnText="Hayır"
                                       confirmBtnBsStyle="danger"
                                       cancelBtnBsStyle="default"
                                       disabled={this.state.disabledDeleteBtn}
                                       title="Emin misiniz?"
                                       onConfirm={() => this.deleteClicked(row)}
                                       onCancel={() => this.setState({alert: null})}
                        >
                            {row.category + " form türü silinecek."}
                        </SweetAlertExt>
                    )
                });
            }
        } else {
            {
                this.setState({
                    alert: (
                        <SweetAlertExt error
                                       confirmBtnText="Tamam"
                                       confirmBtnBsStyle="error"
                                       title="Uyarı"
                                       onConfirm={() => this.setState({alert: null})}>
                            Form şablonunda kullanımda olan form türü silinemez.
                        </SweetAlertExt>
                    )
                });
            }
        }
    }

    renderCreateUpdate() {
        return <Modal isOpen={this.state.displayCreateUpdateDialog} size="md"
                      toggle={() => this.setState({displayCreateUpdateDialog: false})}>
            <ModalHeader className="justify-content-center"
                         toggle={() => this.setState({displayCreateUpdateDialog: false})}>
                Form Türleri
            </ModalHeader>
            <ModalBody>
                {this.state.displayCreateUpdateDialog && <Notify ref={this.notify}/>}
                {this.state.selected && <div>
                    <Form>
                        <FormGroup row>
                            <Label sm={4} for="category">Form Türü: *</Label>
                            <Col sm={8}>
                                <Input type="text" name="category" value={this.state.selected.category}
                                       onChange={this.editHandleInputChange} {...this.editClassName("category")}/>
                            </Col>
                        </FormGroup>
                    </Form>
                </div>}
            </ModalBody>
            <ModalFooter>
                <Button title="İptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                        onClick={() => this.setState({displayCreateUpdateDialog: false})}>
                    İptal
                </Button>
                <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                        disabled={this.state.disabledSaveBtn}
                        onClick={this.saveClicked}>
                    Kaydet
                </Button>
            </ModalFooter>
        </Modal>
    }

    activateJoyride() {
        if (this.state.joyrideActive === false) {
            this.setState({flag: 0});
            this.setState({joyrideActive: true})
            if (this.state.width < 1000) {
                this.setState({flag: 1});
            }
        }
    }

    callback(data) {
        if (data.action === 'close' && data.type === 'step:after') {
            this.setState({shouldRun: false});
            this.setState({flag: 0});
        }
        if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
            this.setState({shouldRun: true});
            this.setState({joyrideActive: false})
            this.setState({flag: 0});
            this.activateJoyride();
        }
    }

    render() {
        let steps = this.state.steps;
        let locale = this.state.locale;

        const columns = [
            {
                Header: "Form Türü",
                accessor: "category",
                Cell: (item) => (<div style={{textAlign: "left"}}>{item.category}</div>),
                width: "80%"
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                width: "10%",
                Cell: row => (
                    <div>
                        <MiniButton title="Düzenle" faIcon="fa-edit" onClick={() => this.editClicked(row)}
                                    color="success"/>
                        <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                    onClick={() => this.countChecker(row)}/>
                    </div>)

            }
        ];

        const fields = [
            {
                label: "Form Türü",
                accessor: "category",
                type: "text",
                operator: "=",
            }
        ];

        return (<div className="row baseBackground">
            {this.state.joyrideActive &&
            <Joyride
                disableBeacon={false}
                autoStart
                locale={locale}
                steps={steps}
                continuous={true}
                run={this.state.shouldRun}
                scrollToFirstStep={true}
                showProgress={true}
                showSkipButton={true}
                scrollToSteps={true}
                disableCloseOnEsc={true}
                disableOverlay={false}
                disableOverlayClicks={false}
                keyboardNavigation={true}
                hideBackButton={false}
                disableOverlayClose={true}
                callback={this.callback}
                holePadding={0}
                shouldRun={this.state.shouldRun}
                styles={{
                    options: {
                        zIndex: 100000,
                        primaryColor: "#50a8e2"   // "#fc9636"
                    }
                }}/>}
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <NavItem>
                    {/*<NavLink href="#" onClick={this.createClicked} style={{color: '#fcfdff', fontSize: '13px'}}>*/}
                    {/*    <img className="simple-text logo-mini" style={{maxWidth: '28px', marginRight: '6px'}}*/}
                    {/*         src={process.env.REACT_APP_CONTEXT_NAME + "/image/plus_sign.png"}/>*/}
                    {/*    Yeni Oluştur*/}
                    {/*</NavLink>*/}
                </NavItem>
                <StickButton position="leftSide" className="zocial GeriDon" style={{fontSize: 'x-small'}}
                             label="Geri" title="Önceki Sayfaya Git"
                             onClick={() => this.props.history.push({pathname: `/${this.props.peykName}/formcreation/`})}/>
                <StickButton position="leftSide" className="zocial Yeni" style={{fontSize: 'x-small'}}
                             label="Yeni Oluştur"
                             onClick={this.createClicked}/>
                {this.state.stop &&
                <div className="helpButtonContainer" position='rightSide'
                     style={{position: "inherit", right: "4.4em", marginTop: "1.4em"}}>
                    <button className="helpButton" onClick={() => {
                        this.activateJoyride()
                    }}>
                        <span className="tooltipText">Size yardımcı olabilirim</span>
                        <i className="far fa-question-circle helpButtonIcon"/>
                    </button>
                </div>
                }
            </Nav>
            <div className="col-sm-12 satirBasi">
                <div className="card-header">
                    <h4 className="card-title">Form Türü Listesi</h4>
                </div>
                <div className="card-body">
                    <DetailedSearch fields={fields}
                                    loading={this.state.searching}
                                    onSearch={this.handleSearch}/>
                </div>
                <div className="card-body">
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           total={this.state.total}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50", "100"]}
                           fetchData={this.fetchData}
                    />
                </div>
            </div>
            {this.state.alert}
            {this.renderCreateUpdate()}
            {!this.state.displayCreateUpdateDialog && <Notify ref={this.notify}/>}
        </div>)
    }
}

export default FormTypePage;